.container__header {
    background-color: $color-primary;
    height: 10rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 150rem;

    @media(max-width:670px) {
        justify-content: baseline;
    }

    &__searchBar {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;

        &__btn {
            @media(max-width: 670px){
                display: none;
            }
        }
    }

    &__logo {
        max-height: 10rem;
        cursor: pointer;
        
        @media(max-width: 1030px) {
            max-height: 7rem;
        }

        @media(max-width: 750px) {
            max-height: 6rem;
        }
    }

    &__buttons {
        display: flex;
        flex: 1;
        justify-content: center;

        @media(max-width:670px) {
            justify-content: flex-end;
            margin-left: 6rem;
        }

        &__addRecipe,
        &__savedRecipes {
            margin: 0 1rem;
        }
    }
}